/* Successful Alert notification toast */

.contact-form_toast__3A3OS {
    visibility: hidden;
    max-width: 100px;
    height: 50px;
    /*margin-left: -125px;*/
    margin: auto;
    background-color: #FF3366;
    color: #fff;
    text-align: center;
    border-radius: 10px;
  
    position: fixed;
    z-index: 1;
    left: 0;
    right: 0;
    bottom: 30px;
    font-size: 17px;
    white-space: nowrap;
  }
  .contact-form_toast__3A3OS .contact-form_img__1ZKZ8 {
    width: 50px;
    height: 50px;
    /* border: 3px solid white; */
    border-radius: 10px;
    float: left;
  
    padding-top: 16px;
    padding-bottom: 16px;
  
    box-sizing: border-box;
  
    background-color: #111;
    color: #fff;
  }
  .contact-form_toast__3A3OS .contact-form_desc__1AJmv {
    color: #fff;
  
    padding: 16px;
  
    overflow: hidden;
    white-space: nowrap;
  }
  
  .contact-form_toast__3A3OS.contact-form_show__39-pw {
    visibility: visible;
    -webkit-animation: contact-form_fadein__2nNro 0.5s, contact-form_expand__nkQXg 0.5s 0.5s, contact-form_stay__3AetY 3s 1s, contact-form_shrink__3dMbl 0.5s 2s,
      contact-form_fadeout__3-Scd 0.5s 2.5s;
    animation: contact-form_fadein__2nNro 0.5s, contact-form_expand__nkQXg 0.5s 0.5s, contact-form_stay__3AetY 3s 1s, contact-form_shrink__3dMbl 0.5s 4s,
      contact-form_fadeout__3-Scd 0.5s 4.5s;
  }
  
  @-webkit-keyframes contact-form_fadein__2nNro {
    from {
      bottom: 0;
      opacity: 0;
    }
    to {
      bottom: 30px;
      opacity: 1;
    }
  }
  
  @keyframes contact-form_fadein__2nNro {
    from {
      bottom: 0;
      opacity: 0;
    }
    to {
      bottom: 30px;
      opacity: 1;
    }
  }
  
  @-webkit-keyframes contact-form_expand__nkQXg {
    from {
      min-width: 50px;
    }
    to {
      min-width: 350px;
    }
  }
  
  @keyframes contact-form_expand__nkQXg {
    from {
      min-width: 50px;
    }
    to {
      min-width: 350px;
    }
  }
  @-webkit-keyframes contact-form_stay__3AetY {
    from {
      min-width: 350px;
    }
    to {
      min-width: 350px;
    }
  }
  
  @keyframes contact-form_stay__3AetY {
    from {
      min-width: 350px;
    }
    to {
      min-width: 350px;
    }
  }
  @-webkit-keyframes contact-form_shrink__3dMbl {
    from {
      min-width: 350px;
    }
    to {
      min-width: 50px;
    }
  }
  
  @keyframes contact-form_shrink__3dMbl {
    from {
      min-width: 350px;
    }
    to {
      min-width: 50px;
    }
  }
  
  @-webkit-keyframes contact-form_fadeout__3-Scd {
    from {
      bottom: 30px;
      opacity: 1;
    }
    to {
      bottom: 60px;
      opacity: 0;
    }
  }
  
  @keyframes contact-form_fadeout__3-Scd {
    from {
      bottom: 30px;
      opacity: 1;
    }
    to {
      bottom: 60px;
      opacity: 0;
    }
  }
  
